<template>
    <div>
        <div>
            <div v-for="item in list" :key="item.id" style="width: 50vw; display:flex;justify-content:space-between">
                <h5>{{item.id}} {{item.title}}</h5>
                <button @click="del(item.title,item.id)" style="height: 3vh;margin-top: 2vh;">delete</button>
            </div>
        </div>
    </div>
</template>

<script>
import global from '@/global.vue';

export default{
    name:'manmage',
    data() {
        return {
            list:[],
        }
    },
    components:{
        global,
    },
    methods:{
        del(title,id)
        {
            var test_value = confirm("你确定要删除《"+title+"》吗？");
            console.log(test_value);
            const json={id:id};
            if(test_value)
            {
                fetch(global.rooturl+"/art/delete/",{
                    method:'POST',
                    body:JSON.stringify(json),
                    headers: 
                    {
                        'Content-Type': 'application/json'
                    },
                })
                .then(res=>res.json())
                .then(data=>{
                    if(data.message==="success")
                    {
                        alert("删除成功")
                    }
                    })
                .then(
                    fetch("http://8.130.96.252/art/select/summary/")
                    .then(res=>res.json())
                    .then(data=>{
                        console.log(data);
                        this.list=data.result;
                        console.log(this.list);
                        })
                    )
            }
            else
            {
                return
            }
            
        }
    },
    beforeCreate()
    {
        fetch("http://8.130.96.252/art/select/summary/")
        .then(res=>res.json())
        .then(data=>{
            console.log(data);
            this.list=data.result;
            console.log(this.list);
        })
    }
}
</script>