<template>
  <!-- ref配置 dom获取属性 -->
  <div>  
    <form action="">
        标题：&nbsp;&nbsp;&nbsp;<input type="text" v-model='title'/><br/>
        供稿人：<input type="text" v-model="contributor"/><br/>
        审稿人：<input type="text" v-model="reviewer"/><br/>
        来源：&nbsp;&nbsp;&nbsp;<input type="text" v-model="origin"/><br/>
        是否置顶：<input type="checkbox" v-model="top" /><br/>

        <table>
        <tr>
            <th> tag:</th>
        </tr>
        <tr>
            <td><input type="checkbox" value="news" v-model="tags" /></td>
            <td>近日新闻</td>
        </tr>
        <tr>
            <td><input type="checkbox" value="announcements" v-model="tags" /></td>
            <td>校园公告</td>
        </tr>
        <tr>
            <td><input type="checkbox" value="communityStyle" v-model="tags" /></td>
            <td>社团风采</td>
        </tr>
        <tr>
            <td><input type="checkbox" value="viewpoint" v-model="tags" /></td>
            <td>视点观察</td>
        </tr>
        <tr>
            <td><input type="checkbox" value="departmentStyle" v-model="tags" /></td>
            <td>院系风采</td>
        </tr>
        <tr>
            <td><input type="checkbox" value="meetingSummary" v-model="tags" /></td>
            <td>会议概要</td>
        </tr>
      </table>

      <br>
      <div>
        <a href="https://imgloc.com/"  target="_blank"  >图床请点击我</a>
      </div>
      <br>
    </form>
  </div>
  <div ref="editorElem" style="text-align: left"></div>

  <div>
    <button @click="sendHtml">发布新闻</button>
  </div>
</template>
 
<script>
import wangeditor from "wangeditor";
export default {
  data() {
    return {
      editor: null,
      editorContent: "",
      title:``,
      top:0,
      reviewer:``,
      contributor:``,
      tags:[],
      origin:``,
    };
  },
  watch: {
    onEditorData(value) {
      console.log(value);
    },
  },
  mounted() {
    //获取dom元素 this.$refs.名字  进入富文本初始化  const editor = new E('#div1')
    this.editor = new wangeditor(this.$refs.editorElem);
    //配置 onchange 函数之后，用户操作（鼠标点击、键盘打字等）导致的内容变化之后，会自动触发 onchange 函数执行
    this.editor.customConfig.onchange = (html) => {
      this.editorContent = html; //获取输入的值
      //把富文本器  里面输入的内容 传递给父组件使用数据
     // console.log(this);
      this.$emit("onEditor", this.editorContent);

    };
    /// 默认情况下，显示所有菜单
    this.editor.customConfig.menus = [
      // 菜单配置
      "head", // 标题
      "bold", // 粗体
      "fontSize", // 字号
      "fontName", // 字体
      "italic", // 斜体
      "underline", // 下划线
      "strikeThrough", // 删除线
      "foreColor", // 文字颜色
      "backColor", // 背景颜色
      "link", // 插入链接
      "list", // 列表
      "justify", // 对齐方式
      "quote", // 引用
      "emoticon", // 表情
      "image", // 插入图片
      "table", // 表格
      "code", // 插入代码
      "undo", // 撤销
      "redo", // 重复
    ];
    //创建富文本实例
    this.editor.create();
    //创建编辑器之后，使用 editor.txt.html(...) 设置编辑器内容   重新设置编辑器内容
    // this.editor.txt.html(this.onEditorData)
  },
  methods:{
    sendHtml(editor){
        console.log("text: "+this.editorContent)
        console.log("title: "+this.title)
        console.log("origin:"+this.origin)
        console.log("top:"+this.top)
        console.log("contributor:"+this.contributor)
        console.log("tags:"+this.tags)
        console.log("reviewerName:"+this.reviewer)

        

        const json={
            "text": this.editorContent,
            "title": this.title,
            "origin": this.origin,
            "status": this.top,
            "contributorName": this.contributor,
            "tagNameList": this.tags,
            "reviewerName": this.reviewer,
        }

        fetch("http://8.130.96.252/art/create",{
            method:'post',
            body:JSON.stringify(json),
            headers: 
                {
                    'Content-Type': 'application/json'
                },
            })
            .then(res=>res.json())
            .then(data =>{
                console.log(data.message)
                if(data.message==="Success")
                {
                    alert("发布成功")
                }
                else
                {
                    alert("出现错误，错误码："+data.code+"\n 请稍后重试或者联系开发者")
                }
            })
    }
  }
};
</script>
 
